import React from 'react';
import s from './Card.module.css';

export default class Card extends React.Component {
    render() {
        return (
            <div className={ s.card }>
                <div className={ s.card__name }>{ this.props.name }</div>
                <img className={ s.card__img } src={ this.props.img } alt=""/>
                <div className={ s.card__price }><span role="img" aria-label="">⚡</span> { this.props.price } руб.</div>
                {
                    this.props.basket.indexOf(this.props.id) !== -1
                    ? <button className={s.card__btn_select} onClick={ () => this.props.delItemOfBasket(this.props.id)}>Удалить из корзины</button>
                    : <button className={s.card__btn_unselect} onClick={ () => this.props.addItemInBasket(this.props.id)}>Добавить в корзину</button>
                }
            </div>
        );
    }
}